
.layout-top {
    height: 60px;
    padding: 10px;
    background: #fff;
}
.layout-left {
    background: #464c5b;
}
.layout-left-header {
    width: 100%;
    height: 50px;
    padding: 15px;
    background: #464c5b;
}
.layout-left-title {
    color: #e8e8e8;
    font-size: 1.4em;
}
.layout-left-toggle {
    float: right;
    width: 30px;
    margin-top: -10px;
    margin-right: 10px;
}
.layout-left-toggle button {
    color: #2D8CF0;
}
.layout-left-nav {
    width: 100%;
}
.layout-right-header {
    height: 50px;
    padding: 15px;
    background: #f5f7f9;
}
.layout-right-content {
    padding: 1px;
    background: #fff;
}
.content-main {
    min-height: 490px;
    padding: 5px;
    background: #f8f8f8;
    border-radius: 4px;
}
.content-main h2 {
    padding: 5px;
}
.content-main h2 i {
   margin-right: 5px;
}
.content-main .split {
    height: 1px;
    border-top: 2px solid #ddd;
    margin-top: 2px;
    margin-bottom: 15px;
}
.layout-copy {
    height: 50px;
    padding: 15px;
    text-align: center;
    color: #808080;
    background: #f5f7f9;
}
.layout-left-hide .nav-hide {
    display: none;
}
.layout-left-hide .ivu-menu-submenu-title-icon {
    display: none;
}
.space {
    margin-left: 10px;
    margin-right: 10px;
}
.ivu-table-small tbody td {
    height: 32px;
}
.ivu-table .ivu-table-cell {
    padding-left: 10px;
    padding-right: 10px;
}
.ivu-table-cell .ivu-checkbox-wrapper {
  margin-right: 0px;
}
.ivu-table .span2 {
    border-right: 0;
}
.ivu-table .failed {
    color: #ff0000;
    font-weight: bold;
}
.ivu-upload-list {
    float: right;
}
.ivu-dropdown .ivu-select-dropdown {
    z-index: 999;
}



